import Vue from 'vue'
import Router from 'vue-router'
import Home from './views/Home.vue'

Vue.use(Router)

export default new Router({
  mode: 'hash',
  base: process.env.BASE_URL,
  routes: [
    {
      path: '/',
      name: 'home',
      component: Home,
      children: [{
        path: 'record',
        name: 'record',
        component: () => import('./views/record')
      }, {
        path: 'table',
        name: 'table',
        component: () => import('./views/table')
      }, {
        path: 'chart',
        name: 'chart',
        component: () => import('./views/chart')
      }]
    }
  ]
})
